import { useContext } from "react";
import TitlePageLogo from "../TitlePageLogo/TitlePageLogo";
import { AppContext } from "../../../../Provider/Provider";

const HomeInfo = () => {
  const { tip } = useContext(AppContext);
  return (
    <div className="homeInfo">
      <div className="info">
        <TitlePageLogo />
        <span className="info_text">
          <img
            src={`${tip}/icons/LogoTipTopCss.svg`}
            alt="logo"
            className="LogoHomeInfo--img"
            width="120"
            height="120"
          />{" "}
          <p style={{ marginTop: 10 }}>
            to firma, ktra jest zwiczeniem 15-letniego dowiadczenia w
            glazurnictwie oraz wykoczeniach wntrz.
          </p>
          <br />
          <p>
            Dziki tak duemu dowiadczeniu nasze usugi s wykonywane
            profesjonalnie, szybko i solidnie.
          </p>
          <br />
          <p>
            Priorytetem dla nas jest dobry kontakt z klientem, dziki czemu
            wsppraca na tej linii przebiega bez najmniejszych komplikacji.
          </p>
        </span>
      </div>
    </div>
  );
};

export default HomeInfo;
